.bookcards {
  padding: 40px;
  position: relative;
  min-height: 400px;
  height: auto;
  border: 1px solid;
  border-color: var(--secondary);
  background-color: var(--primary);
  display: inline-block;
}

.bookcards > .number {
  position: absolute;
  top: 5px;
  right: -15px;
  width: 40px;
  height: 40px;
  font-size: 24px;
  color: var(--secondary);
  font-family: "Montserrat", sans-serif;
}

.bookcards .title {
  font-family: "Montserrat", sans-serif;
  font-size: 36px;
  font-weight: bold;
  color: var(--secondary);
  margin-bottom: 25px;
}

.bookcards .sub {
  font-size: 12px;
  text-transform: uppercase;
  color: var(--secondary);
}

.bookcards .text {
  font-size: 16px;
  color: var(--secondary);
}

button {
  font-family: "Overpass", sans-serif;
  font-size: 14px;
  margin-top: 15px;
  width: 100%;
  height: 18px;
  background-color: var(--primary);
  border: none;
  color: var(--secondary);
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
  align-content: center;
}

button > .material-icons {
  font-size: 18px;
}
